<template>
  <div>
    <transition name="router" mode="out-in">
      <a-spin :spinning="loading">
        <router-view />
      </a-spin>
    </transition>
  </div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  data() {
    return {
      loading: false  
    }
  },
  methods: {
    ...mapActions('booksManage', {
      getAllBookCategory: 'getAllBookCategory'
    }),
  },
  created() {
    this.loading = true
    this.getAllBookCategory().finally(() => {
      this.loading = false
    })
  }
}
</script>